.loader {
  border-radius: 50%;
  border: 6px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-bottom: 4px solid #3498db;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
  margin: 20px 0;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
